@import "variables.scss";

.App {
  position: relative;
  background: $black;
  background-image: linear-gradient(to top, rgba(0, 0, 0, .5) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, .5) 100%), url("./images/Pattern.png");

  &__container {
    padding-top: 36px;
    max-width: 1120px;
    margin: 0 auto;
    text-align: center;

  }

  &__title {
    text-shadow: 0 1px 0 $black;
    color: $white;
    font-family: "Exo 2.0- Thin";
    font-size: 36px;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 25px;
  }
}

